import React from 'react'
import About from "./About";
import Contact from './Contact';
import Project from "./Project";
import Skills from "./Skills"
import Navbar from "../Components/Navbar"
import {
 
    Button,
    Box,
    Flex,
    Stack,
    Heading,
    Text,
    Image
  
  } from '@chakra-ui/react'


const Homepage = () => {
  return (
    <div>
        <Navbar/>
        <Box w="100%" bgColor="#319795" h={{ base:"700px" , md :"700px", lg: "500px" }}  >
    <Box h="100%" w="85%"   bgColor="#319795" margin="auto" paddingTop="50px" alignItems="center" >
      <Flex justifyContent="space-between" direction={{ base:"column" , md :"column", lg: "row" }} >
   <Box w={{ base:"90%" , md :"90%", lg: "row" }} bgColor="#319795"  textAlign="left"  paddingTop="80px"  >
   <Text color="#E2E8F0" >Hi Myself </Text>
     <Heading color="white" >Akshay kumar</Heading>
     <Text color="#1D4044" >I love to work on web application using technologies like React, JavaScript, Chakra UI,CSS etc.</Text>
   </Box>
   <Box  w={{ base:"90%" , md :"50%", lg: "row" }}bgColor="#319795" margin="auto" paddingTop="50px" paddingLeft="50px" paddingBottom="50px" >
  <Image w="300px" src="https://avatars.githubusercontent.com/u/107462235?v=4" borderRadius="20%" />
   </Box>
   </Flex>
    </Box>
    </Box>
        <About/>
        <Contact/>
        <Project/>
        <Skills/>
    </div>
  )
}

export default Homepage