import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    useDisclosure,
    Button,
    Box,
    Flex,
    Stack

  } from '@chakra-ui/react'

  import { HamburgerIcon } from '@chakra-ui/icons';
  import { NavLink, } from 'react-router-dom';

export default  function Modalsec() {
    const { isOpen, onOpen, onClose } = useDisclosure()
    return (
      
        <>
        <HamburgerIcon  color="white"  w={6} h={6}  onClick={onOpen} />
       
        <Modal isOpen={isOpen} onClose={onClose}  >
          <ModalOverlay  />
          <ModalContent  w={{ base: "80%", md :"80%", lg: '90%' }} bgColor="#319795" >
            <ModalHeader></ModalHeader>
            <ModalCloseButton />
            <ModalBody>
            <Flex flexDirection="column"  textAlign="center" >
            <Stack spacing={8} >
            <NavLink  style={{color:"white"}}  to="/" >About</NavLink>
        <NavLink style={{color:"white"}} to="/contact" >Contact</NavLink>
         <NavLink style={{color:"white"}} to="/project" >Project</NavLink>
         <NavLink style={{color:"white"}} to="/skills"  >Skills</NavLink>
         <Button bgColor="#CBD5E0"  colorScheme='whatsapp' border="1px solid green" variant='outline'>RESUME</Button>
         </Stack>
         </Flex>
            </ModalBody>
  
            <ModalFooter>
              
            </ModalFooter>
          </ModalContent>
        </Modal>
       
        </>
      
    )
  }