import React from 'react'
import { NavLink } from 'react-router-dom';
import { Box,Text,Button } from '@chakra-ui/react';
import Modalsec from "./Modal"

const Navbar = () => {
  return (
    <Box 
     bgColor="#319795"
      w={{ base: "100%", md :"100%", lg: '100%' }}>
        <Box 
         w={{ base: "90%", md :"90%", lg: '90%' }} 
         alignItems="center" display="flex" 
          justifyContent="space-between"  margin="auto">
          <Box w={{ base: "45%", md :"30%", lg: '30%' }}  textAlign="left"  >
          <Text  fontSize='2xl' color="#1A202C" fontWeight="bold"  >PORTFOLIO</Text>
          </Box>
          <Box w={{ base: "70%", md :"80%", lg: '70%' }}  display={{ base: "none", md :"flex", lg: "flex"}}
           justifyContent="space-between" alignItems="center" >

         <NavLink  style={{color:"white"}}  to="/about" >About</NavLink>
        <NavLink style={{color:"white"}} to="/contact" >Contact</NavLink>
         <NavLink style={{color:"white"}} to="/project" >Project</NavLink>
         <NavLink style={{color:"white"}} to="/skills"  >Skills</NavLink>
         <Button bgColor="#CBD5E0"  colorScheme='whatsapp' border="1px solid green" variant='outline'>RESUME</Button>
         
         </Box>
         <Box display={{ base: "flex", md :"none", lg: "none"}}  >
        <Modalsec />
         </Box>
        
        </Box>
    </Box>
  )
}

// {/* <NavLink  style={{color:"white"}}  to="/" >About</NavLink>
//         <NavLink style={{color:"white"}} to="/contact" >Contact</NavLink>
//         <NavLink style={{color:"white"}} to="/project" >Project</NavLink>
//         <NavLink style={{color:"white"}} to="/skills"  >Skills</NavLink>
//         <Button bgColor="#CBD5E0"  colorScheme='whatsapp' border="1px solid green" variant='outline'>RESUME</Button> */}



export default Navbar

